body {
  color: #222;
  background-color: #f8f8f8;
  margin: 0;
  font-family: Arial, sans-serif;
}

header {
  color: #fff;
  background-color: #c62828;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  display: flex;
}

header h1 {
  margin: 0;
}

.controls {
  align-items: center;
  gap: 10px;
  display: flex;
}

#locationSelect, #searchBar {
  border: none;
  border-radius: 4px;
  outline: none;
  padding: 8px;
}

#signInBtn {
  color: #c62828;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 4px;
  padding: 8px 14px;
  font-weight: bold;
}

#signInBtn:hover {
  background-color: #ffe5e5;
}

main {
  padding: 20px;
}

.scroll-container {
  align-items: center;
  display: flex;
  position: relative;
}

.movie-container {
  scroll-behavior: smooth;
  scrollbar-width: none;
  gap: 15px;
  padding: 30px 0;
  display: flex;
  overflow-x: auto;
}

.movie-container::-webkit-scrollbar {
  display: none;
}

.movie-card {
  cursor: pointer;
  background: #fff;
  border-radius: 10px;
  flex: none;
  width: 400px;
  transition: transform .3s;
  overflow: hidden;
  box-shadow: 0 2px 5px #0003;
}

.movie-card:hover {
  transform: scale(1.05);
}

.movie-card img {
  object-fit: cover;
  width: 100%;
  height: 380px;
}

.movie-info {
  padding: 10px;
}

.movie-info h3 {
  margin: 0 0 5px;
  font-size: 18px;
}

.movie-info p {
  color: #555;
  margin: 2px 0;
  font-size: 14px;
}

.scroll-btn {
  color: #fff;
  cursor: pointer;
  z-index: 2;
  background-color: #c62828cc;
  border: none;
  border-radius: 50%;
  padding: 8px;
  font-size: 20px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

#scrollLeft {
  left: -10px;
}

#scrollRight {
  right: -10px;
}
